/**
 * Counter Animation Utilities
 * Handles number counter animations and stats
 */

export class CounterUtils {
  /**
   * Initialize all counters
   */
  static init() {
    // Counter for statistics
    const counters = document.querySelectorAll('[data-counter]');
    
    if (counters.length === 0) {
      console.log('âš ï¸ No counter elements found');
      return;
    }

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          console.log('ðŸ“Š Starting counter animation for:', entry.target.getAttribute('data-counter'));
          CounterUtils.animateCounter(entry.target);
          observer.unobserve(entry.target);
        }
      });
    }, {
      threshold: 0.5, // Trigger when 50% of the element is visible
      rootMargin: '0px 0px -50px 0px' // Trigger a bit before the element is fully visible
    });

    counters.forEach(counter => {
      observer.observe(counter);
    });

    console.log(`âœ… Counter utilities initialized (${counters.length} counters found)`);
  }

  /**
   * Animate counter from 0 to target value
   * @param {HTMLElement} element - The counter element
   */
  static animateCounter(element) {
    const target = parseInt(element.getAttribute('data-counter'));
    const duration = parseInt(element.getAttribute('data-duration')) || 2000;
    
    if (isNaN(target)) {
      console.error('âŒ Invalid counter target value:', element.getAttribute('data-counter'));
      return;
    }

    console.log(`ðŸ”¢ Animating counter to ${target} over ${duration}ms`);
    
    const startTime = performance.now();
    let current = 0;

    const animate = (currentTime) => {
      const elapsed = currentTime - startTime;
      const progress = Math.min(elapsed / duration, 1);
      
      // Use easing function for smoother animation
      const easeOut = 1 - Math.pow(1 - progress, 3);
      current = Math.floor(target * easeOut);
      
      element.textContent = CounterUtils.formatNumber(current);
      
      if (progress < 1) {
        requestAnimationFrame(animate);
      } else {
        // Ensure we end with the exact target value
        element.textContent = CounterUtils.formatNumber(target);
        console.log(`âœ… Counter animation completed: ${target}`);
      }
    };

    requestAnimationFrame(animate);
  }

  /**
   * Format number with appropriate suffix (K, M, etc.)
   * @param {number} num - The number to format
   * @returns {string} - Formatted number
   */
  static formatNumber(num) {
    if (num >= 1000000) {
      return (num / 1000000).toFixed(1) + 'M';
    } else if (num >= 1000) {
      return (num / 1000).toFixed(1) + 'K';
    }
    return num.toString();
  }

  /**
   * Set counter value manually
   * @param {HTMLElement} element - The counter element
   * @param {number} value - The new value
   */
  static setCounterValue(element, value) {
    element.setAttribute('data-counter', value);
    element.textContent = CounterUtils.formatNumber(value);
  }

  /**
   * Reset all counters
   */
  static resetAllCounters() {
    const counters = document.querySelectorAll('[data-counter]');
    counters.forEach(counter => {
      counter.textContent = '0';
    });
  }

  /**
   * Force initialize counters (for manual triggering)
   */
  static forceInit() {
    console.log('ðŸ”§ Force initializing counters...');
    const counters = document.querySelectorAll('[data-counter]');
    
    counters.forEach(counter => {
      if (counter.textContent === '0' || counter.textContent === '') {
        CounterUtils.animateCounter(counter);
      }
    });
    
    console.log(`ðŸ”§ Force initialized ${counters.length} counters`);
  }
}
